const createNewElement = (htmlString) => {
    const tempElement = document.createElement('div');
    tempElement.innerHTML = htmlString;

    return tempElement.firstElementChild;
}

const createFullCard = (cityName, countryName, description, imagePath, isReverse = false) => createNewElement(`
    <div class="full-card ${isReverse ? "full-card_order_reverse" : ''} js-full-card">
        <img class="full-card__picture" src="${imagePath}" alt="The city you chose">
        <p class="current__city disposition_absolute">${cityName}</p>
        <p class="current__country disposition_absolute">${countryName}</p>
        <div class="card-about">
            <p class="full-card__text">
                ${description}
            </p>
            <a class="card-about__button" href="#">–ü–æ–¥—Ä–æ–±–Ω–µ–µ</a>
            <button class="icon"><img class="icon__img" src="${closeSvg}"></button>
        </div>
    </div>
`);


const currentCity = document.querySelector(".current__city"); // –ì–æ—Ä–æ–¥
const currentCountry = document.querySelector(".current__country"); // –°—Ç—Ä–∞–Ω–∞
const fullCardText = document.querySelector(".full-card__text"); // –ò–Ω—Ñ—Ä–æ–º–∞—Ü–∏—è –æ —Å—Ç—Ä–∞–Ω–µ
const fullCardPicture = document.querySelector(".full-card__picture") // –ö–∞—Ä—Ç–∏–Ω–∫–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
const cardImage = document.querySelector(".image");

const countryCards = document.querySelectorAll(".js-card-container"); // –í—Å–µ –∫–Ω–æ–ø–∫–∏ –∫–∞—Ä—Ç–æ—á–µ–∫
let fullCard = document.querySelector(".full-card"); // –¶–µ–ª–∞—è –∫–∞—Ä—Ç–æ—á–∫–∞ –¥–ª—è —Ç–æ–≥–æ, —á—Ç–æ–±—ã –æ–Ω–∞ –º–æ–≥–ª–∞ –∏—Å—á–µ–∑–∞—Ç—å


countryCards.forEach((countryCard) => {
    countryCard.addEventListener("click", event => {
        let cityInformation = countryCard.dataset.infoAbtCity;
        const countryData = travels[cityInformation];
        const imagePath = countryCard.querySelector('img').src;
        const fullCountryCard = createFullCard(countryData.city, countryData.country, countryData.information, imagePath);
        countryCard.append(fullCountryCard);
    });
})

